    about1:"MG Engineering Company LLP has been formed in August, 2002 at the initiative of a group of specialists worked in oli and gas and construction branches of industry for a long time. Untli December, 2013, the company was known as Munai Grafika. However, after completion of the corporate restructuring, it has been renamed and continues operating under a new name reflecting the main trend of the professional activity.",

